.modal-header {
  border-bottom: 0;
  padding: 0; 
}

.modal-dialog {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: calc(100vh - 30px); 
}
.modal-dialog .modal-content {
  border: none;
  background-color: #333;
  border-radius: 11px !important;
  width: 100%; 
}

button.close {
  text-shadow: none;
  color: #fff;
  font-weight: normal;
  font-size: 42px;
  transition: all 0.2s ease-in-out;
  opacity: 0.5; 
}
button.close:focus {
  outline: none; 
}
button.close:active {
  outline: none; 
}
button.close:hover {
  color: #fff;
  transition: all 0.2s ease-in-out;
  opacity: 0.2; 
}

.showsDetails {
  min-height: 500px;
  background-size: cover !important;
  border-radius: 10px; 
}


.showsDetailsContainer .showsDetailsOverlay {
  position: absolute;
  padding: 24px 32px;
  font-size: 14px;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 1;
  border-radius: 10px;
  box-shadow: none;
  color: #DAE0D4;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0.9) 100%);
  -webkit-transition: all .3s ease-in-out;
  -ms-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out; 
}

.showsSchedule {
  padding-bottom: 20px; 
}
.showsSchedule ul {
  margin: 0;
  padding: 0;
  color: #DAE0D4;
  font-weight: bold;
  text-transform: uppercase; 
}
.showsSchedule ul li {
  list-style-type: none;
  display: inline;
  margin: 0;
  padding: 0;
  color: #fff;
  font-weight: normal;
  text-transform: none; 
}
.showsSchedule ul li:after {
  content: ', '; 
}
.showsSchedule ul li:last-child:after {
  content: ""; 
}

@media (min-width: 992px) {
  .showsTitle {
    font-weight: bold;
    font-size: 54px;
    line-height: normal;
    margin-bottom: 5px;
    color: #fff; 
  }
  .showsName {
    display: inline; 
  }
}

@media (max-width: 991px) {
  .showsTitle {
    font-weight: bold;
    font-size: 24px;
    margin-bottom: 5px;
    color: #fff; 
  }
  .showsSubtitle {
    padding-bottom: 20px;
  }
  .showsName {
    display: block; 
  }
}

.showsOverview {
  text-align: justify;
  display: block;
  display: -webkit-box;
  max-height: 160px;
  font-size: 13px;
  line-height: 1.4;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis; 
}