body {
  margin: 0;
  padding-left: 0;
  padding-right: 0;
  padding-bottom: 0;
  font-family: 'Roboto', Arial;
  word-wrap: break-word;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; 
}

a {
  color: #fff;
  font-weight: bold;
  text-decoration: none; 
}
a:hover {
  color: #fff; 
}
