@keyframes fadein {
  from {
    opacity: 0;
    transform: scale(1.05); 
  }
  to {
    opacity: 1;
    transform: scale(1); 
  } 
}

.App {
  color: #fff;
  margin-bottom: 20px; 
}

.App.dark {
	background: #000;
	color: #fff;
	transition: 0.3s all;
}

.App.light {
	background: #fff;
	color: #000;
	transition: 0.3s all;
}

.App-header {
  background-color: #222;
  height: 55px;
  padding: 20px;
  color: white; 
}

.App-title {
  font-size: 1.5em; 
}

.App-intro {
  font-size: large; 
}

a {
  outline: none; 
}

.flex-item {
  display: flex;
  align-items: center; 
}

.flex-item img {
  flex-grow: 0;
  flex-shrink: 0; 
}

.no-padding {
  padding-right: 0 !important;
  padding-left: 0 !important; 
}

.navbar {
  min-height: 60px; 
}

.navbar-brand {
  display: block;
  width: 270px;
  margin: 0 auto; 
}
.navbarLogo {
  width: 25px;
  height: 25px;
  margin-top: -8px;
  margin-right: 4px;
}

.navbar-default {
  background-color: #22252a !important;
  background-image: none !important;
  border: none !important;
  border-radius: 0 !important;
  box-shadow: none !important; 
}

.thumb {
  padding: 10px 0;
  width: 200px;
  margin: 0 auto;
  animation: .8s ease-in-out 0s 1 fadein; 
}

.thumbImage {
  display: block;
  height: 300px;
  width: 200px;
  border-radius: 10px;
  margin: 10px 0;
  background-position: center;
  background-size: cover !important; 
}

.thumbImageContainer:hover .thumbImage {
  transform: scale(0.95);
  -webkit-transition: transform .4s ease-in-out;
  -ms-transition: transform .4s ease-in-out;
  transition: transform .4s ease-in-out;
}

.thumbImageContainer .thumbImage {
  transform: scale(1);
  -webkit-transition: transform .4s ease-in-out;
  -ms-transition: transform .4s ease-in-out;
  transition: transform .4s ease-in-out; 
}

.thumbImageContainer {
  position: relative; 
}

.thumbImageOverlayContainer {
  cursor: pointer;
}
.thumbImageContainer .thumbImageOverlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 1;
  border-radius: 10px;
  box-shadow: none;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 40%, rgba(0, 0, 0, 0.8) 100%);
  -webkit-transition: all .4s ease-in-out;
  -ms-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  color: #fff; 
}

.thumbImageContainer:hover .thumbImageOverlay {
  display: block;
  opacity: 0;
  transform: scale(0.95);
  -webkit-transition: all .4s ease-in-out;
  -ms-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out; 
}

.thumbTitle {
  font-weight: bold;
  overflow: hidden;
  white-space: nowrap;
  font-size: 14px; 
}

.thumbAirtime {
  font-size: 12px; 
}
